body {
  margin: 0;
  font-family: serif;
}

#satelliteDisplay {
  position: fixed;
  top: 0;
  right: 0;
  border-left: 1px solid #333;
  border-bottom: 1px solid #333;

  .caption {
    position: absolute;
    top: 0;
    left: 0;
    padding: 4px;
    margin: 0;
    color: white;
    background: #333;
  }
}